<script>
    export default {
        data() {
            return {
                induk:[],
                indukBarang:[],
                NoBet:'',
                kodeBarang1:'',
                kodeBarang2:'',
                kodeBarang3:'',
                kodeBarang4:'',
                NoRules: [
                    value => (value || '').length == 4 || 'INPUTAN HARUS 4 Karakter',
                ],
                nomor:'',
                nomorUji:'',
                keyword: '',
                jumlahData: 1,
                load: false,
                headers: [{
                        text: 'No',
                        value: 'no',
                    },
                    {
                        text: 'Tanggal',
                        value: 'Tanggal'
                    },
                    {
                        text: 'No Bukti',
                        value: 'No Bukti'
                    },
                    {
                        text: 'Nama Suplier',
                        value: 'Nama Suplier'
                    },
                    {
                        text: 'Nama Barang',
                        value: 'Nama Barang'
                    },
                    {
                        text: 'Quantity',
                        value: 'Quantity'
                    },
                    {
                        text: 'Harga',
                        value: 'Harga'
                    },
                    {
                        text: 'Sub Total',
                        value: 'Sub Total'
                    },
                    {
                        text: 'Total',
                        value: 'Total'
                    },
                ],
                dbelicpkbs: [],
                dbelicpkbforms: [],
                spesifikasiforms: [],
                dbelicpkbforms2: [],
                spesifikasiforms2: [],
                dbelicpkbforms3: [],
                spesifikasiforms3: [],
                dbelicpkbforms4: [],
                spesifikasiforms4: [],
                judul:'',
                judul2:'',
                judul3:'',
                judul4:'',
                total: 0,
            }
        },
        methods: {
            getDataNoBet() {
                var uri = this.$apiUrl + 'mutstokcpkbs/produksi'
                this.$http.get(uri).then(response => {
                    this.induk = response.data
                })
            },
            getDataBarang() {
                var uri = this.$apiUrl + 'mutstokcpkbs/produksi/' + this.NoBet
                this.$http.get(uri).then(response => {
                    this.indukBarang = response.data
                })
            },
            getDataSpesifikasi() {
                if(this.kodeBarang1){
                    var uri = this.$apiUrl + 'spesifikasiuji/form/' + this.kodeBarang1["Kode Barang"]
                    this.$http.get(uri).then(response => {
                        this.spesifikasiforms = response.data.data[0]
                    })
                }
                if(this.kodeBarang2){
                    var uri = this.$apiUrl + 'spesifikasiuji/form/' + this.kodeBarang2["Kode Barang"]
                    this.$http.get(uri).then(response => {
                        this.spesifikasiforms2 = response.data.data[0]
                    })
                }
                if(this.kodeBarang3){
                    var uri = this.$apiUrl + 'spesifikasiuji/form/' + this.kodeBarang3["Kode Barang"]
                    this.$http.get(uri).then(response => {
                        this.spesifikasiforms3 = response.data.data[0]
                    })
                }
                if(this.kodeBarang4){
                    var uri = this.$apiUrl + 'spesifikasiuji/form/' + this.kodeBarang4["Kode Barang"]
                    this.$http.get(uri).then(response => {
                        this.spesifikasiforms4 = response.data.data[0]
                    })
                }
            },
            


            printReport() {
                window.print()
            },

            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
        },
        mounted() {
            this.getDataNoBet();
        },
    }
</script>
